@import url(https://fonts.googleapis.com/css2?family=Varela&display=swap);
.header{border:1px solid #c3073f;border-radius:25px;background:#1a1a1d;height:45px;padding:10px;display:flex;justify-content:space-between}.logo{display:flex;font-weight:bold;align-items:center}.logo>*{margin-left:3px}.navbar{display:flex}.navbar a{border:1px solid #c3073f;border-radius:25px;transition:background-color 0.4s linear;font-size:14px;margin-right:10px;color:#fff;cursor:pointer;padding:5px}.navbar a.activeLink{background-color:#c3073f}.navbar a:hover{background-color:#c3073f}.profile{display:flex;flex-direction:column;border:1px solid #c3073f;border-radius:25px;background-color:#1a1a1d;border-width:0 1px 1px 1px;height:600px;padding:5px}.profile__img{display:flex;justify-content:center;align-items:center;background-color:#c3073f;margin-left:20px;border-radius:50px;width:100px;height:100px}.profile__status{background-color:#c3073f;font-weight:bold}.profile__personal{display:flex;flex-direction:column}.profile__personal>*{border:1px solid #c3073f;border-radius:25px;width:150px;text-align:center;margin-top:10px}.profile__posts{overflow:auto;display:flex;flex-direction:column-reverse}.profile__posts__title{font-weight:900;text-align:center;font-size:24px}.profile__post{transition:background-color 0.4s linear;display:flex;align-items:center;margin-top:10px;border-radius:25px}.profile__post__img{display:flex;justify-content:center;align-items:center;background-color:#c3073f;border-radius:25px;width:30px;height:30px}.profile__post__content{margin-left:5px}.profile__post:hover{background-color:#c3073f;cursor:pointer}.dialogs{display:flex}.dialogs__items{display:flex;flex-direction:column;width:300px;font-weight:bold}.dialogs__item{border:1px solid #c3073f;border-radius:25px;transition:background-color 0.4s linear;display:flex;align-items:center}.dialogs__item:hover{background-color:#c3073f;cursor:pointer}.dialogs__item.active{background-color:#c3073f}.dialogs__name{margin:0 auto}.dialogs__img{display:flex;justify-content:center;align-items:center;border:1px solid #c3073f;background-color:#000;border-radius:50px;width:30px;height:30px}.dialogs__window{display:flex;flex-direction:column;border:1px solid #c3073f;border-radius:25px;border-collapse:separate;background-color:#1a1a1d;align-items:flex-start;text-align:center;height:600px;overflow:auto}.dialogs__general{width:100%}.dialogs__message{border:1px solid #c3073f;border-radius:25px;justify-self:center;padding:5px}.dialogs__message.in{background-color:#c3073f;margin:2px 5px 5px 10px;border-bottom-left-radius:1px}.dialogs__message.out{background-color:#1a1a1d;margin:5px 10px 5px 10px;border-bottom-right-radius:1px;align-self:flex-end}.input-field{border:1px solid #c3073f;border-radius:25px;display:flex;margin-top:5px}.input-field.msg{justify-self:center;height:45px}.input-field textarea,.input-field button{font-size:16px}.input-field textarea{background-color:#1a1a1d;padding-left:15px;border:0;resize:none;border-radius:25px;width:100%;outline:none}.input-field textarea:focus::-webkit-input-placeholder{color:transparent}.input-field textarea:focus:-ms-input-placeholder{color:transparent}.input-field textarea:focus::placeholder{color:transparent}.input-field button{display:flex;justify-content:center;align-items:center;border:1px solid #c3073f;border-radius:25px;transition:background-color 0.4s linear;background-color:#1a1a1d;width:120px;cursor:pointer;outline:none}.input-field button:hover{background-color:#c3073f}.input-field button.msg{border:0;width:70px}::-webkit-scrollbar{width:20px}::-webkit-scrollbar-thumb{border:1px solid #c3073f;border-radius:25px 700px 700px 25px}::-webkit-scrollbar-thumb:hover{margin:50px;background:#c3073f}*{color:white;font-family:'Varela', 'sans-serif';margin:0;padding:0;box-sizing:border-box}a{text-decoration:none;color:#fff}body{background:radial-gradient(circle, #bb1f4e 0%, #1a1a1d 55%) no-repeat fixed;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.wrapper{height:100%;max-width:100%}

